      </div>

      <div class="create-modal-content" v-if="selectedReportForEvidence">
        <!-- Report Info Section -->
        <div class="mb-6 p-4 bg-gradient-to-r from-blue-50 to-indigo-50 border border-blue-200 rounded-xl">
          <div class="flex items-start space-x-3">
            <div class="w-10 h-10 bg-blue-500 rounded-full flex items-center justify-center flex-shrink-0">
              <Icon icon="mdi:file-document" class="w-5 h-5 text-white" />
            </div>
            <div class="flex-1 min-w-0">
              <h4 class="text-sm font-medium text-blue-900 mb-1">
                {{ getFieldValue(selectedReportForEvidence, 'name', 'nama', 'title') || 'Laporan Tanpa Nama' }}
              </h4>
              <div class="space-y-1">
                <div class="flex items-center text-xs text-blue-700">
                  <Icon icon="mdi:map-marker" class="w-3 h-3 mr-1" />
                  <span>{{ getFieldValue(selectedReportForEvidence, 'place', 'lokasi', 'location') }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- Tab Navigation -->
        <div class="mb-6">
          <div class="border-b border-gray-200">
            <nav class="-mb-px flex space-x-8" aria-label="Tabs">
              <button
                @click="activeEvidenceTab = 'upload'"
                :class="[
                  activeEvidenceTab === 'upload'
                    ? 'border-green-500 text-green-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300',
                  'whitespace-nowrap py-2 px-1 border-b-2 font-medium text-sm flex items-center transition-colors'
                ]"
              >
                <Icon icon="mdi:cloud-upload" class="w-4 h-4 mr-2" />
                Upload Evidence
              </button>
              <button
                @click="activeEvidenceTab = 'list'"
                :class="[
                  activeEvidenceTab === 'list'
                    ? 'border-green-500 text-green-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300',
                  'whitespace-nowrap py-2 px-1 border-b-2 font-medium text-sm flex items-center transition-colors'
                ]"
              >
                <Icon icon="mdi:file-multiple" class="w-4 h-4 mr-2" />
