        totalActiveReports: 0
      },
      statisticsLoading: false
    }
  },
  computed: {
    filteredReports() {
      let filtered = this.reports;

      // Filter by search query
      if (this.searchQuery) {
        const query = this.searchQuery.toLowerCase();
        filtered = filtered.filter(report => 
          report.name.toLowerCase().includes(query) ||
          report.code.toLowerCase().includes(query) ||
          report.description.toLowerCase().includes(query) ||
          report.place.toLowerCase().includes(query)
        );
      }

      // Filter by category
      if (this.selectedCategory) {
        filtered = filtered.filter(report => 
          report.category === this.selectedCategory
        );
      }

      // Filter only active and non-deleted reports
      filtered = filtered.filter(report => 
        report.isActived && !report.isDeleted
      );

      return filtered;
    },
    totalPages() {
      // Use server-side pagination
      return this.pagination.totalPages;
    },
    displayedReports() {
      // For client-side filtering, return filtered results
      // For server-side pagination, return all current reports
      return this.searchQuery || this.selectedCategory ? this.filteredReports : this.reports;
    }
  },
  async mounted() {
    await this.fetchReports();
    this.fetchStatistics();
    
    // Add escape key listener
    document.addEventListener('keydown', this.handleKeydown);
  },
  beforeDestroy() {
    // Remove escape key listener
    document.removeEventListener('keydown', this.handleKeydown);
  },
  methods: {
    async fetchReports() {
      this.loading = true;
      this.error = null;
      
      try {
        const result = await reportsService.getMyReports(this.currentPage, this.itemsPerPage);
        console.log('Full API result:', result);

        this.reports = result.content || [];
        this.pagination = result.pagination || {
          totalElements: 0,
          totalPages: 0,
          first: true,
          last: true,
          empty: false
        };
        
        console.log('Reports loaded:', this.reports.length);
        console.log('Sample report structure:', this.reports[0]);
        console.log('Pagination info:', this.pagination);
      } catch (error) {
        console.error('Error fetching reports:', error);
        this.error = error.message;
        this.reports = [];
        this.pagination = {
          totalElements: 0,
          totalPages: 0,
          first: true,
          last: true,
          empty: false
        };
      } finally {
        this.loading = false;
      }
    },

    async fetchStatistics() {
      this.statisticsLoading = true;
      
      try {
        console.log('Fetching reports statistics...');
        const result = await reportsService.getReportsStatistics();
        
        console.log('Statistics result:', result);
        
        if (result && result.success && result.data) {
          this.statistics = result.data;
          console.log('Statistics loaded successfully:', this.statistics);
        } else {
          console.warn('Invalid statistics response:', result);
        }
      } catch (error) {
        console.error('Error fetching statistics:', error);
        // Don't show error message for statistics, just use fallback
      } finally {
        this.statisticsLoading = false;
      }
    },
    async refreshReports() {
      await this.fetchReports();
      this.fetchStatistics();
      // Keep current page for refresh
    },
    formatDateTime(dateTime) {
      if (!dateTime) return 'Tidak diketahui';
      
      try {
        const date = new Date(dateTime);
        return date.toLocaleString('id-ID', {
          year: 'numeric',
          month: 'long',
          day: 'numeric',
          hour: '2-digit',
          minute: '2-digit'
        });
      } catch (error) {
        return dateTime; // Return original if parsing fails
      }
    },
    getStatusClass(status) {
      const statusClasses = {
        'Dibatalkan': 'bg-red-100 text-red-800',
        'Dikirim': 'bg-blue-100 text-blue-800',
        'Disaring': 'bg-yellow-100 text-yellow-800',
        'Investigasi Awal': 'bg-orange-100 text-orange-800',
        'Dilaporkan': 'bg-purple-100 text-purple-800',
        'Investigasi Lanjutan': 'bg-indigo-100 text-indigo-800',
        'Buat Tim': 'bg-teal-100 text-teal-800',
        'Buat Laporan': 'bg-cyan-100 text-cyan-800',
        'Keputusan Terakhir': 'bg-green-100 text-green-800'
      };
      return statusClasses[status] || 'bg-gray-100 text-gray-800';
    },
    getStatusText(status) {
      switch (status?.toLowerCase()) {
        case 'pending':
        case 'menunggu':
          return 'Menunggu';
        case 'approved':
        case 'disetujui':
          return 'Disetujui';
        case 'rejected':
        case 'ditolak':
          return 'Ditolak';
        case 'in progress':
        case 'sedang diproses':
          return 'Sedang Diproses';
        case 'completed':
        case 'selesai':
          return 'Selesai';
        default:
          return status || 'Status Tidak Diketahui';
      }
    },
    viewReport(report) {
      console.log('View report clicked:', report);
      
      // Open modal and fetch detailed report data
      this.showModal = true;
      
      // Check if report has id field
      const reportId = report.id;
      if (!reportId) {
        console.error('Report ID is missing:', report);
        this.modalError = 'ID laporan tidak valid';
        return;
      }
      
      this.fetchReportDetail(reportId);
    },
    async fetchReportDetail(reportId) {
      if (!reportId) {
        this.modalError = 'ID laporan tidak valid';
        return;
      }

      this.modalLoading = true;
      this.modalError = null;
      this.selectedReport = null;

      try {
        console.log('Fetching report detail for ID:', reportId);
        const result = await reportsService.getReport(reportId);
        console.log('Report detail result:', result);
        console.log('Report detail structure:', result.report);
        
        if (result && result.report) {
          this.selectedReport = result.report;
          console.log('Selected report set to:', this.selectedReport);
          
          // Log available fields
          console.log('Available fields in selectedReport:', Object.keys(result.report));
        } else {
          this.modalError = 'Data laporan tidak ditemukan';
        }
      } catch (error) {
        console.error('Error fetching report detail:', error);
        this.modalError = error.message || 'Gagal memuat detail laporan';
      } finally {
        this.modalLoading = false;
      }
    },
    closeModal() {
      this.showModal = false;
      this.selectedReport = null;
      this.modalError = null;
      this.modalLoading = false;
    },
    handleKeydown(event) {
      if (event.key === 'Escape' && (this.showModal || this.showCreateModal || this.showDeleteModal || this.showEditModal)) {
        if (this.showDeleteModal) {
          this.closeDeleteModal();
        } else if (this.showEditModal) {
          this.closeEditModal();
        } else if (this.showModal) {
          this.closeModal();
        } else if (this.showCreateModal) {
          this.closeCreateModal();
        }
      }
    },
    openCreateModal() {
      this.showCreateModal = true;
      this.resetCreateForm();
    },
    closeCreateModal() {
      this.showCreateModal = false;
      this.createError = null;
      this.createLoading = false;
      this.resetCreateForm();
    },
    resetCreateForm() {
      this.newReport = {
        nama_pelapor: '',
        tanggal_waktu: '',
        lokasi: '',
        kategori: 'belum pilih',
        deskripsi: ''
      };
      this.errors = {};
      this.selectedCategoryDescription = '';
    },
    async createReport() {
      this.errors = {};
      
      if (!this.validateCreateForm()) {
        return;
      }

      this.isCreatingReport = true;
      
      try {
        // Format data untuk API
        const reportData = {
          name: this.newReport.nama_pelapor,
          time: this.newReport.tanggal_waktu,
          place: this.newReport.lokasi,
          category: this.newReport.kategori,
          description: this.newReport.deskripsi,
          status: 'Open'
        };

        const result = await reportsService.createReport(reportData);
        
        if (result.success) {
          this.closeCreateModal();
          this.resetCreateForm();
          
          // Refresh data
          await this.fetchReports();
          
          // Show success message
          this.$toast && this.$toast.success 
            ? this.$toast.success(result.message || 'Laporan berhasil dibuat')
            : alert(result.message || 'Laporan berhasil dibuat');
        }
      } catch (error) {
        console.error('Error creating report:', error);
        
        if (error.errors && typeof error.errors === 'object') {
          this.errors = error.errors;
        } else {
          this.$toast && this.$toast.error 
            ? this.$toast.error(error.message || 'Gagal membuat laporan')
            : alert(error.message || 'Gagal membuat laporan');
        }
      } finally {
        this.isCreatingReport = false;
      }
    },
    validateCreateForm() {
      this.errors = {};
      let isValid = true;

      if (!this.newReport.nama_pelapor.trim()) {
        this.errors.nama_pelapor = 'Nama pelapor wajib diisi';
        isValid = false;
      }
      
      if (!this.newReport.tanggal_waktu) {
        this.errors.tanggal_waktu = 'Tanggal dan waktu wajib diisi';
        isValid = false;
      }
      
      if (!this.newReport.lokasi.trim()) {
        this.errors.lokasi = 'Lokasi wajib diisi';
        isValid = false;
      }
      
      if (!this.newReport.kategori || this.newReport.kategori === 'belum pilih') {
        this.errors.kategori = 'Kategori wajib dipilih';
        isValid = false;
      }
      
      if (!this.newReport.deskripsi.trim()) {
        this.errors.deskripsi = 'Deskripsi wajib diisi';
        isValid = false;
      }

      return isValid;
    },
    // Helper method to get field value with multiple possible field names
    getFieldValue(obj, ...fieldNames) {
      for (const fieldName of fieldNames) {
        if (obj && obj[fieldName] !== undefined && obj[fieldName] !== null && obj[fieldName] !== '' && obj[fieldName] !== 'Tidak ada') {
          return obj[fieldName];
        }
      }
      return '-';
    },

    getStatusCount(status) {
      const statusLower = status.toLowerCase();
      return this.reports.filter(report => {
        const reportStatus = this.getFieldValue(report, 'status', 'state').toLowerCase();
        return reportStatus === statusLower;
      }).length;
    },

    getDetailStatusClass(status) {
      const statusClasses = {
        'Dibatalkan': 'bg-red-500 text-white',
        'Dikirim': 'bg-blue-500 text-white',
        'Disaring': 'bg-yellow-500 text-white',
        'Investigasi Awal': 'bg-orange-500 text-white',
        'Dilaporkan': 'bg-purple-500 text-white',
        'Investigasi Lanjutan': 'bg-indigo-500 text-white',
        'Buat Tim': 'bg-teal-500 text-white',
        'Buat Laporan': 'bg-cyan-500 text-white',
        'Keputusan Terakhir': 'bg-green-500 text-white'
      };
      return statusClasses[status] || 'bg-gray-500 text-white';
    },
    // Method to handle category selection and show description
    onCategoryChange() {
      if (this.newReport.kategori && this.newReport.kategori !== 'belum pilih') {
        const categoryData = ingredientData.jenis_pelanggaran.find(
          item => item.title === this.newReport.kategori
        );
        this.selectedCategoryDescription = categoryData ? categoryData.description : '';
      } else {
        this.selectedCategoryDescription = '';
      }
    },
    onCategoryUpdateChange() {
      if (this.editForm.category && this.editForm.category !== 'belum pilih') {
        const categoryData = ingredientData.jenis_pelanggaran.find(
          item => item.title === this.editForm.category
        );
        this.selectedCategoryDescription = categoryData ? categoryData.description : '';
      } else {
        this.selectedCategoryDescription = '';
      }
    },
    deleteReport(report) {
      this.reportToDelete = report;
      this.showDeleteModal = true;
      this.deleteConfirmationText = '';
    },

    closeDeleteModal() {
      this.showDeleteModal = false;
      this.reportToDelete = null;
      this.deleteLoading = false;
      this.deleteConfirmationText = '';
    },

    async confirmDeleteReport() {
      if (this.deleteConfirmationText !== 'HAPUS') {
        this.$message.warning('Silakan ketik "HAPUS" untuk mengkonfirmasi penghapusan');
        return;
      }

      this.deleteLoading = true;
      
      try {
        // Call API delete endpoint
        await reportsService.deleteReport(this.reportToDelete.id);
        
        this.$message.success({
          message: `Laporan "${this.getFieldValue(this.reportToDelete, 'name', 'nama', 'title')}" berhasil dihapus`,
          duration: 3000,
          showClose: true
        });
        
        // Close modal and refresh data
        this.closeDeleteModal();
        
        // Close detail modal if the deleted report is currently being viewed
        if (this.selectedReport && this.selectedReport.id === this.reportToDelete.id) {
          this.closeModal();
        }
        
        await this.fetchReports();
        
      } catch (error) {
        console.error('Error deleting report:', error);
        this.$message.error({
          message: `Gagal menghapus laporan: ${error.message}`,
          duration: 5000,
          showClose: true
        });
      } finally {
        this.deleteLoading = false;
      }
    },

    // Evidence Modal Methods
    showEvidenceModal(report) {
      console.log('showEvidenceModal called with report:', report);
      this.selectedReportForEvidence = report;
      this.evidenceModalVisible = true;
      this.evidenceForm.reportId = report.id;
      this.activeEvidenceTab = 'upload'; // Reset to upload tab
      this.resetEvidenceForm();
      
      // Fetch evidence list for this report
      this.fetchEvidenceList(report.id);
      
      console.log('evidenceModalVisible set to:', this.evidenceModalVisible);
    },

    closeEvidenceModal() {
      this.evidenceModalVisible = false;
      this.selectedReportForEvidence = null;
      this.evidenceUploading = false;
      this.evidenceUploadError = null;
      this.evidenceList = [];
      this.evidenceListLoading = false;
      this.evidenceListError = null;
      this.activeEvidenceTab = 'upload'; // Reset to upload tab
      this.resetEvidenceForm();
    },

    resetEvidenceForm() {
      this.evidenceForm = {
        file: null,
        description: '',
        reportId: this.evidenceForm.reportId || null
      };
      this.evidenceFileList = [];
      this.evidenceErrors = {
        file: '',
        description: ''
      };
    },

    handleFileChange(file) {
      // Validate file size (10MB max)
      const maxSize = 10 * 1024 * 1024; // 10MB in bytes
      if (file.size > maxSize) {
        this.evidenceErrors.file = 'Ukuran file maksimal 10MB';
        this.evidenceFileList = [];
        return;
      }

      // Validate file type
      const allowedTypes = ['image/jpeg', 'image/png', 'image/gif', 'application/pdf', 
                           'application/msword', 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'];
      if (!allowedTypes.includes(file.raw.type)) {
        this.evidenceErrors.file = 'Format file tidak didukung. Gunakan JPG, PNG, PDF, DOC, atau DOCX';
        this.evidenceFileList = [];
        return;
      }

      this.evidenceForm.file = file.raw;
      this.evidenceFileList = [file];
      this.evidenceErrors.file = '';
    },

    // New methods for native file input
    triggerFileInput() {
      this.$refs.fileInput.click();
    },

    handleFileInputChange(event) {
      const file = event.target.files[0];
      if (file) {
        this.processFile(file);
      }
    },

    handleFileDrop(event) {
      this.isDragOver = false;
      const files = event.dataTransfer.files;
      if (files.length > 0) {
        this.processFile(files[0]);
      }
    },

    handleDragOver(event) {
      this.isDragOver = true;
    },

    handleDragLeave(event) {
      this.isDragOver = false;
    },

    processFile(file) {
      // Validate file size (10MB max)
      const maxSize = 10 * 1024 * 1024; // 10MB in bytes
      if (file.size > maxSize) {
        this.evidenceErrors.file = 'Ukuran file maksimal 10MB';
        return;
      }

      // Validate file type
      const allowedTypes = ['image/jpeg', 'image/png', 'image/gif', 'application/pdf', 
                           'application/msword', 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'];
      if (!allowedTypes.includes(file.type)) {
        this.evidenceErrors.file = 'Format file tidak didukung. Gunakan JPG, PNG, PDF, DOC, atau DOCX';
        return;
      }

      this.evidenceForm.file = file;
      this.evidenceErrors.file = '';
    },

    removeFile() {
      this.evidenceForm.file = null;
      this.$refs.fileInput.value = '';
      this.evidenceErrors.file = '';
    },

    formatFileSize(bytes) {
      if (bytes === 0) return '0 Bytes';
      const k = 1024;
      const sizes = ['Bytes', 'KB', 'MB', 'GB'];
      const i = Math.floor(Math.log(bytes) / Math.log(k));
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
    },

    // Additional evidences service methods
    async fetchEvidencesByReportId(reportId) {
      try {
        const result = await evidencesService.getEvidencesByReportId(reportId);
        if (result.success) {
          return result.data;
        } else {
          console.error('Failed to fetch evidences:', result.error);
          return [];
        }
      } catch (error) {
        console.error('Error fetching evidences:', error);
        return [];
      }
    },

    async fetchEvidenceList(reportId) {
      if (!reportId) {
        console.error('Report ID is required to fetch evidence list');
        return;
      }

      this.evidenceListLoading = true;
      this.evidenceListError = null;

      try {
        // console.log('Fetching evidence list for report ID:', reportId);
        const result = await evidencesService.getEvidenceListByReportId(reportId);

        // console.log('Evidence list result:', result);

        if (result.success) {
          this.evidenceList = result.data.data || [];
          console.log('Evidence list loaded:', this.evidenceList.length, 'items');
        } else {
          this.evidenceListError = result.error || 'Failed to load evidence list';
          this.evidenceList = [];
        }
      } catch (error) {
        console.error('Error fetching evidence list:', error);
        this.evidenceListError = error.message || 'Error loading evidence list';
        this.evidenceList = [];
      } finally {
        this.evidenceListLoading = false;
      }
    },

    async deleteEvidence(evidenceId) {
      try {
        const result = await evidencesService.deleteEvidence(evidenceId);
        if (result.success) {
          this.$message.success('Evidence berhasil dihapus');
          // Refresh reports if needed
          await this.fetchReports();
          return true;
        } else {
          this.$message.error(result.error || 'Gagal menghapus evidence');
          return false;
        }
      } catch (error) {
        console.error('Error deleting evidence:', error);
        this.$message.error('Terjadi kesalahan saat menghapus evidence');
        return false;
      }
    },

    async downloadEvidence(evidenceId, filename) {
      try {
        const result = await evidencesService.downloadEvidence(evidenceId);
        if (result.success) {
          // Create blob URL and trigger download
          const blob = new Blob([result.data]);
          const url = window.URL.createObjectURL(blob);
          const link = document.createElement('a');
          link.href = url;
          link.download = filename || 'evidence-file';
          document.body.appendChild(link);
          link.click();
          window.URL.revokeObjectURL(url);
          document.body.removeChild(link);
          
          this.$message.success('Evidence berhasil didownload');
        } else {
          this.$message.error(result.error || 'Gagal mendownload evidence');
        }
      } catch (error) {
        console.error('Error downloading evidence:', error);
        this.$message.error('Terjadi kesalahan saat mendownload evidence');
      }
    },

    validateEvidenceForm() {
      this.evidenceErrors = {
        file: '',
        description: ''
      };

      if (!this.evidenceForm.file) {
        this.evidenceErrors.file = 'Silakan pilih file evidence';
        return false;
      }

      return true;
    },

    async uploadEvidence() {
      if (!this.validateEvidenceForm()) {
        return;
      }

      this.evidenceUploading = true;
      this.evidenceUploadError = null;

      try {
        console.log('Uploading evidence:', {
          file: this.evidenceForm.file.name,
          description: this.evidenceForm.description,
          reportId: this.evidenceForm.reportId
        });

        // Call evidencesService to upload the file
        const result = await evidencesService.uploadEvidence(
          this.evidenceForm.file,
          this.evidenceForm.description.trim(),
          this.evidenceForm.reportId
        );

        if (result.success) {
          this.$message.success({
            message: 'Evidence berhasil diupload',
            duration: 3000,
            showClose: true
          });

          // Close modal and reset form
          this.closeEvidenceModal();
          
          // Refresh the reports list to show updated data
          await this.fetchReports();
          
          // Refresh the evidence list to show the new evidence
          await this.fetchEvidenceList(this.evidenceForm.reportId);
        } else {
          throw new Error(result.error || 'Upload failed');
        }

      } catch (error) {
        console.error('Error uploading evidence:', error);
        this.evidenceUploadError = error.message || 'Gagal mengupload evidence';
      } finally {
        this.evidenceUploading = false;
      }
    },

    // Evidence list utility methods
    getFileIcon(filename) {
      if (!filename) return 'mdi:file-outline';
      
      const extension = filename.toLowerCase().split('.').pop();
      switch (extension) {
        case 'pdf':
          return 'mdi:file-pdf-box';
        case 'doc':
        case 'docx':
          return 'mdi:file-word-box';
        case 'jpg':
        case 'jpeg':
        case 'png':
        case 'gif':
          return 'mdi:file-image-box';
        default:
          return 'mdi:file-outline';
      }
    },

    async downloadEvidenceFile(evidence) {
      // Set loading state for specific evidence
      this.$set(this.evidenceDownloading, evidence.id, true);
      
      try {
        console.log('Downloading evidence:', evidence);
        const result = await evidencesService.downloadEvidence(
          evidence.id, 
          evidence.filename || evidence.fileName
        );
        
        if (result.success) {
          this.showNotificationV2('success', 'Download Berhasil', `Evidence "${evidence.filename || evidence.fileName}" berhasil didownload`);
        } else {
          this.$message.error(result.error || 'Gagal mendownload evidence');
        }
      } catch (error) {
        console.error('Error downloading evidence:', error);
        this.$message.error('Terjadi kesalahan saat mendownload evidence');
      } finally {
        // Clear loading state
        this.$delete(this.evidenceDownloading, evidence.id);
      }
    },

    async deleteEvidenceFile(evidence) {
      try {
        // Show confirmation dialog
        const confirmed = await this.$confirm(
          `Apakah Anda yakin ingin menghapus evidence "${evidence.filename || evidence.fileName}"?`,
          'Konfirmasi Hapus Evidence',
          {
            confirmButtonText: 'Ya, Hapus',
            cancelButtonText: 'Batal',
            type: 'warning',
            confirmButtonClass: 'el-button--danger'
          }
        );

        if (confirmed) {
          // Set loading state for specific evidence
          this.$set(this.evidenceDeleting, evidence.id, true);
          
          try {
            const result = await evidencesService.deleteEvidence(evidence.id);
            
            if (result.success) {
              this.$message.success('Evidence berhasil dihapus');
              
              // Refresh the evidence list
              await this.fetchEvidenceList(this.selectedReportForEvidence.id);
              
              // Refresh the reports list if needed
              await this.fetchReports();
            } else {
              this.$message.error(result.error || 'Gagal menghapus evidence');
            }
          } finally {
            // Clear loading state
            this.$delete(this.evidenceDeleting, evidence.id);
          }
        }
      } catch (error) {
        if (error !== 'cancel') {
          console.error('Error deleting evidence:', error);
          this.$message.error('Terjadi kesalahan saat menghapus evidence');
        }
        // Clear loading state in case of error
        this.$delete(this.evidenceDeleting, evidence.id);
      }
    },

    editReport(report) {
      // Open edit modal and pre-fill data
      this.editingReport = report;
      this.prepareEditForm(report);
      this.showEditModal = true;
    },
    prepareEditForm(report) {
      // Map report data to edit form with flexible field matching
      this.editForm = {
        name: this.getFieldValue(report, 'name', 'nama', 'nama_pelapor', 'title') || '',
        time: this.getFieldValue(report, 'time', 'tanggal_waktu', 'datetime', 'created_at') || '',
        place: this.getFieldValue(report, 'place', 'lokasi', 'location', 'tempat') || '',
        category: this.getFieldValue(report, 'category', 'kategori', 'type') || 'belum pilih',
        description: this.getFieldValue(report, 'description', 'deskripsi', 'detail', 'content') || '',
        information: this.getFieldValue(report, 'information', 'informasi_tambahan', 'additional_info') || '',
        item: this.getFieldValue(report, 'item', 'barang', 'item_terkait') || '',
        status: this.getFieldValue(report, 'status', 'state', 'condition') || 'Process'
      };
      
      // Reset error state
      this.updateError = null;
    },
    closeEditModal() {
      this.showEditModal = false;
      this.editingReport = null;
      this.updateError = null;
      this.isUpdatingReport = false;
      
      // Reset form
      this.editForm = {
        name: '',
        time: '',
        place: '',
        category: 'belum pilih',
        description: '',
        information: '',
        item: '',
        status: 'Dikirim'
      };
    },
    async updateReportData() {
      if (!this.editingReport || !this.editingReport.id) {
        this.updateError = 'Data laporan tidak valid';
        return;
      }

      // Basic validation
      if (!this.editForm.name.trim()) {
        this.updateError = 'Nama laporan harus diisi';
        return;
      }

      if (!this.editForm.time) {
        this.updateError = 'Waktu kejadian harus diisi';
        return;
      }

      if (!this.editForm.place.trim()) {
        this.updateError = 'Lokasi kejadian harus diisi';
        return;
      }

      if (!this.editForm.category || this.editForm.category === 'belum pilih') {
        this.updateError = 'Kategori harus dipilih';
        return;
      }

      if (!this.editForm.description.trim()) {
        this.updateError = 'Deskripsi harus diisi';
        return;
      }

      this.isUpdatingReport = true;
      this.updateError = null;

      try {
        // Prepare data for API
        const updateData = {
          name: this.editForm.name.trim(),
          time: this.editForm.time,
          place: this.editForm.place.trim(),
          category: this.editForm.category,
          description: this.editForm.description.trim(),
          information: this.editForm.information.trim() || null,
          item: this.editForm.item.trim() || null,
          status: this.editForm.status
        };

        console.log('Updating report with data:', updateData);
        
        const result = await reportsService.updateReport(this.editingReport.id, updateData);

        if (result.success) {
          // Close edit modal
          this.closeEditModal();

          // Close detail modal if the updated report is currently being viewed
          if (this.selectedReport && this.selectedReport.id === this.editingReport.id) {
            this.closeModal();
          }

          // Refresh the reports list
          await this.fetchReports();
          
          // Show success message
          this.$toast?.success?.(result.message || 'Laporan berhasil diupdate') || 
          alert(result.message || 'Laporan berhasil diupdate');
        }
        
      } catch (error) {
        console.error('Error updating report:', error);
        this.updateError = error.message || 'Gagal mengupdate laporan';
      } finally {
        this.isUpdatingReport = false;
      }
    },
    changePage(page) {
      // Convert from 1-based display to 0-based API
      const newPage = page - 1;
      if (newPage >= 0 && newPage < this.totalPages) {
        this.currentPage = newPage;
        this.fetchReports();
      }
    },

    // Step Modal V2 Methods
    openStepModalV2() {
      this.showStepModalV2 = true;
      this.currentStepV2 = 1;
      this.resetStepFormV2();
      this.stepErrorV2 = null;
      this.createdReportIdV2 = null;
      this.stepEvidenceFilesV2 = [];
      this.stepEvidenceProgressV2 = {};
      
      // Add keyboard event listener
      this.$nextTick(() => {
        document.addEventListener('keydown', this.handleStepModalKeydown);
      });
    },

    closeStepModalV2() {
      this.showStepModalV2 = false;
      this.currentStepV2 = 1;
      this.resetStepFormV2();
      this.stepErrorV2 = null;
      this.createdReportIdV2 = null;
      this.stepEvidenceFilesV2 = [];
      this.stepEvidenceProgressV2 = {};
      
      // Remove keyboard event listener
      document.removeEventListener('keydown', this.handleStepModalKeydown);
    },

    resetStepFormV2() {
      this.stepFormV2 = {
        reporterName: '',
        time: '',
        place: '',
        category: 'belum pilih',
        description: ''
      };
      this.stepErrorsV2 = {
        reporterName: '',
        time: '',
        place: '',
        category: '',
        description: ''
      };
    },

    validateStepFormV2() {
      let isValid = true;
      this.stepErrorsV2 = {
        reporterName: '',
        time: '',
        place: '',
        category: '',
        description: ''
      };

      // Validate Nama Pelapor
      if (!this.stepFormV2.reporterName?.trim()) {
        this.stepErrorsV2.reporterName = 'Nama pelapor wajib diisi';
        isValid = false;
      }

      // Validate Tanggal dan Waktu
      if (!this.stepFormV2.time) {
        this.stepErrorsV2.time = 'Tanggal dan waktu wajib diisi';
        isValid = false;
      }

      // Validate Lokasi
      if (!this.stepFormV2.place?.trim()) {
        this.stepErrorsV2.place = 'Lokasi kejadian wajib diisi';
        isValid = false;
      }

      // Validate Kategori
      if (!this.stepFormV2.category || this.stepFormV2.category === 'belum pilih') {
        this.stepErrorsV2.category = 'Kategori wajib dipilih';
        isValid = false;
      }

      // Validate Deskripsi
      if (!this.stepFormV2.description?.trim()) {
        this.stepErrorsV2.description = 'Deskripsi wajib diisi';
        isValid = false;
      }

      return isValid;
    },

    async proceedToStep2V2() {
      if (!this.validateStepFormV2()) {
        this.$message.warning('Mohon lengkapi semua field yang wajib diisi');
        return;
      }

      this.stepLoadingV2 = true;
      this.stepErrorV2 = null;

      try {
        // Prepare report data - include reporter name in item field
        const reportData = {
          name: this.stepFormV2.reporterName.trim(),
          time: this.stepFormV2.time,
          place: this.stepFormV2.place.trim(),
          category: this.stepFormV2.category,
          description: this.stepFormV2.description.trim(),
          status: 'Dikirim',
          information: `Dilaporkan oleh: ${this.stepFormV2.reporterName.trim()}`
          // item: this.stepFormV2.reporterName.trim()
        };

        const result = await reportsService.createReport(reportData);

        console.log(result);

        if (result.success) {
          this.createdReportIdV2 = result.report.id;
          this.currentStepV2 = 2;
          this.showNotificationV2('success', 'Laporan Dibuat', 'Laporan berhasil dibuat! Silakan tambahkan evidence jika ada.');
          
          console.log('Report created with ID:', this.createdReportIdV2);
          
          // Fetch evidence list for the new report
          if (this.createdReportIdV2) {
            await this.fetchEvidenceListV2(this.createdReportIdV2);
          }
        } else {
          this.stepErrorV2 = result.error || 'Gagal membuat laporan';
        }
      } catch (error) {
        console.error('Error creating report:', error);
        this.stepErrorV2 = 'Terjadi kesalahan saat membuat laporan';
      } finally {
        this.stepLoadingV2 = false;
      }
    },

    // Step 2 Methods
    handleFileSelectV2(event) {
      const files = Array.from(event.target.files);
      this.addFilesV2(files);
      event.target.value = '';
    },

    handleDropV2(event) {
      event.preventDefault();
      this.isDragOver = false;
      const files = Array.from(event.dataTransfer.files);
      this.addFilesV2(files);
    },

    addFilesV2(files) {
      files.forEach(file => {
        // Use the new validation method
        if (!this.validateFile(file)) {
          return;
        }

        // Check if file already exists
        const existingFile = this.stepEvidenceFilesV2.find(f => f.name === file.name && f.size === file.size);
        if (existingFile) {
          this.$message.warning(`File ${file.name} sudah dipilih.`);
          return;
        }

        // Add description property
        file.description = '';
        this.stepEvidenceFilesV2.push(file);
      });
    },

    removeFileV2(index) {
      this.stepEvidenceFilesV2.splice(index, 1);
      this.$delete(this.stepEvidenceProgressV2, index);
    },

    formatFileSizeV2(bytes) {
      if (bytes === 0) return '0 Bytes';
      const k = 1024;
      const sizes = ['Bytes', 'KB', 'MB', 'GB'];
      const i = Math.floor(Math.log(bytes) / Math.log(k));
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
    },

    async uploadAllEvidenceV2() {
      if (this.stepEvidenceFilesV2.length === 0) {
        this.$message.warning('Tidak ada file untuk diupload');
        return;
      }

      if (!this.createdReportIdV2) {
        this.$message.error('ID laporan tidak ditemukan. Silakan buat laporan terlebih dahulu.');
        return;
      }

      this.stepEvidenceUploadingV2 = true;
      let successCount = 0;
      let errorCount = 0;

      try {
        for (let i = 0; i < this.stepEvidenceFilesV2.length; i++) {
          const file = this.stepEvidenceFilesV2[i];
          
          try {
            this.$set(this.stepEvidenceProgressV2, i, 0);

            const result = await evidencesService.uploadEvidence(
              file,
              file.description || `Evidence untuk laporan oleh ${this.stepFormV2.reporterName}`,
              this.createdReportIdV2
            );

            if (result.success) {
              this.$set(this.stepEvidenceProgressV2, i, 100);
              successCount++;
            } else {
              errorCount++;
              this.$message.error(`Gagal upload ${file.name}: ${result.error}`);
            }
          } catch (error) {
            errorCount++;
            console.error(`Error uploading ${file.name}:`, error);
            this.$message.error(`Error upload ${file.name}`);
          }
        }

        // Show summary
        if (successCount > 0) {
          this.showNotificationV2('success', 'Upload Berhasil', `Berhasil upload ${successCount} file evidence`);
        }
        if (errorCount > 0) {
          this.showNotificationV2('warning', 'Upload Sebagian Gagal', `${errorCount} file gagal diupload`);
        }

        // Refresh evidence list
        await this.fetchEvidenceListV2(this.createdReportIdV2);

      } finally {
        this.stepEvidenceUploadingV2 = false;
      }
    },

    async fetchEvidenceListV2(reportId) {
      try {
        const result = await evidencesService.getEvidenceListByReportId(reportId);
        if (result.success) {
          this.evidenceList = result.data || [];
        }
      } catch (error) {
        console.error('Error fetching evidence list:', error);
      }
    },

    async deleteEvidenceFileV2(evidence) {
      try {
        // Show confirmation dialog
        const confirmed = await this.$confirm(
          `Apakah Anda yakin ingin menghapus evidence "${evidence.filename || evidence.fileName}"?`,
          'Konfirmasi Hapus Evidence',
          {
            confirmButtonText: 'Ya, Hapus',
            cancelButtonText: 'Batal',
            type: 'warning',
            confirmButtonClass: 'el-button--danger'
          }
        );

        if (confirmed) {
          // Set loading state for specific evidence
          this.$set(this.evidenceDeleting, evidence.id, true);
          
          try {
            const result = await evidencesService.deleteEvidence(evidence.id);
            
            if (result.success) {
              this.showNotificationV2('success', 'Evidence Dihapus', 'Evidence berhasil dihapus');
              
              // Refresh the evidence list for Step Modal V2
              if (this.createdReportIdV2) {
                await this.fetchEvidenceListV2(this.createdReportIdV2);
              }
            } else {
              this.$message.error(result.error || 'Gagal menghapus evidence');
            }
          } catch (deleteError) {
            console.error('Error deleting evidence:', deleteError);
            this.$message.error('Terjadi kesalahan saat menghapus evidence');
          } finally {
            // Clear loading state
            this.$delete(this.evidenceDeleting, evidence.id);
          }
        }
      } catch (error) {
        if (error !== 'cancel') {
          console.error('Error in deleteEvidenceFileV2:', error);
          this.$message.error('Terjadi kesalahan saat menghapus evidence');
        }
      }
    },

    skipEvidenceV2() {
      this.finishReportV2();
    },

    async finishReportV2() {
      // Refresh reports list
      await this.fetchReports();
      
      this.$message.success('Laporan berhasil dibuat!');
      this.closeStepModalV2();
    },

    // Helper methods for better user experience
    formatFileSize(bytes) {
      if (bytes === 0) return '0 Bytes';
      const k = 1024;
      const sizes = ['Bytes', 'KB', 'MB', 'GB'];
      const i = Math.floor(Math.log(bytes) / Math.log(k));
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
    },

    getFileIconClass(fileName) {
      if (!fileName) return 'el-icon-document';
      const extension = fileName.split('.').pop().toLowerCase();
      const icons = {
        pdf: 'el-icon-document',
        doc: 'el-icon-document',
        docx: 'el-icon-document',
        jpg: 'el-icon-picture',
        jpeg: 'el-icon-picture',
        png: 'el-icon-picture',
        gif: 'el-icon-picture',
        mp4: 'el-icon-video-camera',
        avi: 'el-icon-video-camera',
        zip: 'el-icon-folder-opened',
        rar: 'el-icon-folder-opened',
        default: 'el-icon-document'
      };
      return icons[extension] || icons.default;
    },

    showNotificationV2(type, title, message) {
      this.$notify({
        title,
        message,
        type,
        duration: 3000,
        position: 'top-right'
      });
    },

    // Validate file before upload
    validateFile(file) {
      const maxSize = 10 * 1024 * 1024; // 10MB
      const allowedTypes = [
        'image/jpeg', 'image/png', 'image/gif',
        'application/pdf',
        'application/msword',
        'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
        'video/mp4', 'video/avi',
        'application/zip', 'application/x-rar-compressed'
      ];

      if (file.size > maxSize) {
        this.$message.error(`File ${file.name} terlalu besar. Maksimal 10MB.`);
        return false;
      }

      if (!allowedTypes.includes(file.type)) {
        this.$message.error(`Tipe file ${file.name} tidak didukung.`);
        return false;
      }

      return true;
    },

    // Evidence viewer methods
    viewEvidenceFile(evidence) {
      this.selectedEvidenceForView = evidence;
      this.showEvidenceViewer = true;
    },

    handleEvidenceViewerClose() {
      this.showEvidenceViewer = false;
      this.selectedEvidenceForView = {};
    },

    // Check if file is an image
    isImageFile(filename) {
      if (!filename) return false;
      const imageExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.bmp', '.webp'];
      return imageExtensions.some(ext => filename.toLowerCase().endsWith(ext));
    },

    // Keyboard shortcut handler for step modal V2
    handleStepModalKeydown(event) {
      if (!this.showStepModalV2) return;
      
      // ESC to close modal
      if (event.key === 'Escape') {
        this.closeStepModalV2();
        return;
      }
      
      // Enter to proceed or submit (only if not in textarea)
      if (event.key === 'Enter' && !event.shiftKey && event.target.tagName !== 'TEXTAREA') {
        event.preventDefault();
        
        if (this.currentStepV2 === 1) {
          // Proceed to step 2
          this.proceedToStep2V2();
        } else if (this.currentStepV2 === 2 && this.stepEvidenceFilesV2.length > 0) {
          // Upload files
          this.uploadAllEvidenceV2();
        }
      }
      
      // Tab navigation between form fields
      if (event.key === 'Tab' && this.currentStepV2 === 1) {
        // Let default tab behavior work
        return;
      }
    }
  },
  watch: {
    searchQuery() {
      // For client-side filtering, no need to fetch from server
      // Just filter the current results
    },
    selectedCategory() {
      // For client-side filtering, no need to fetch from server
      // Just filter the current results
    }
  }
}
</script>

